
/*galeria*/

.portafolio{
    background: #f2f2f2;
}

.galeria-port{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.imagen-port{
    width: 24%;
    margin-bottom: 10px;
    gap: 5px;
    height: 200px;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, .5);
    
}

.imagen-port img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transition: filter .2s;

}

.imagen-port:hover{
    transform: scale(1.2);
   z-index: 1;
}

